.work__title {
    display: grid;
    grid-template-columns: auto 1fr auto;
    margin-top: 30px;
}

.hr {
    border-style: dashed;
    margin: 0;
    margin-bottom: 5px;
    width: 0%;
}

.work__title__column:nth-child(1) {
    font-size: 27px;
    white-space: nowrap;
    opacity: 0;
}

.work__title__column img {
    height: 20px;
    width: 20px;
    margin-left: 10px;
    margin-top: 9px;
}
.work__title__column:nth-child(2) {
    margin-left: 40px;
    margin-right: 10px;
    align-self: flex-end;
}

.work__title__column:nth-child(3) {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    opacity: 0;
}

.work__wrapper {
    margin-top: 50px;
    margin-bottom: 70px;
}

.work {
    display: flex;
}

.work__column:nth-child(1) {
    flex: 1 1 270px;
    border-right: 2px solid;
    position: relative;
}

.work__column:nth-child(1):after {
    content: '';
    position: absolute;
    top: 0;
    right: -9px;
    height: 14px;
    background-color: white;
    width: 14px;
    border-radius: 50%;
    border: 1px solid
}
.title {
    display: none;
}
.work__column:nth-child(2) {
    flex: 1 1 100%;
    padding-left: 30px;
}

.work__column > span {
    position: relative;
    top: -5px;
    left: 25px;
    font-size: 14px;
    font-weight: 600;
}

.work__column img {
    object-fit: cover;
    border-radius: 50%;
}

.info {
    margin-left: 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}


.link {
    margin-top: 0;
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 5px;
    color: inherit;
    text-decoration: none;
    border-bottom: 1px solid;
    
}

.video__link {
    background-color: transparent;
    outline: none;
    border: none;
    padding: 0;
    font-weight: 400;
    cursor: pointer;
    border-bottom: 1px solid;
}

.description {
    font-size: 17px;
    margin-top: 10px;
}

.technology {
    position: relative;
    top: -5px
}

.main__info {
    min-height: 88px;
}

.technology span {
    font-weight: 600;
    font-size: 15px;
} 

.ready__site {
    display: flex;   
    padding-bottom: 35px;
    padding-top: 35px
}

.ready__site:last-child {
    display: flex;   
    padding-bottom: 0;
}

.button {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
}

.center {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
    margin-top: -10px;
}

.gitHub__links {
    position: relative;
    border-radius: 50%;
}

.gitHub__links img {
    border-radius: 50%;
}


.gitHub__links > div {
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    background-color: rgba(0,0,0,0.6);
    color:white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 140px;
    width: 140px;
    opacity: 0.3;
}
.gitHub__links_big > div {
    height: 170px;
    width: 170px;
}

@media (max-width: 992px) {
    .description {
        font-size: 15px;
    }
    .technology, .technology span {
        font-size: 15px;
    }
    .height {
        height: 25px !important;
    }

    .info {
        margin-left: 25px;
    }
    .work__column:nth-child(2) {
        padding-left: 20px;
    }

    .gitHub__links > div {
        position: static; 
        background-color: transparent;
        flex-direction: column;
        height: auto;
        width: 140px;
        margin-top: 10px;
        margin-bottom: 20px;
        opacity: 1 !important;
    } 
}

@media (max-width: 768px) {
    .work__column:nth-child(2) {
        padding-left: 0px;
    }
    .work__column:nth-child(1) {
        display: none;
    }
    .work__title__column:nth-child(1) {
        font-size: 20px;
        align-self: flex-end;
    }
    .work__title__column:nth-child(2) {
        margin-left: 20px;
        margin-right: 15px;
    }
    .work__title__column:nth-child(3) {
        height: 35px;
        width: 35px;
    }
    .work__title__column img {
        height: 16px;
        width: 16px;
        margin-left: 9px;
        margin-top: 8px;
    }
    .title {
        display: block; 
        font-weight: 600;
        margin-bottom: 10px;
        text-align: center;
        font-size: 20px;
    }
    .title:first-child {
        margin-top: -20px;
    }
}

@media (max-width: 600px) {
    .info {
        margin-left: 20px;
    }
    .link {
        font-size: 15px;
        font-weight: 600;
    }
    .description {
        font-size: 14px;
    }
    .technology, .technology span {
        font-size: 14px;
    }    
}

@media (max-width: 480px) { 
    .ready__site {
        flex-direction: column;
        align-items: center;
    }
    .ready__site img {
        margin-bottom: 15px;
    }
    .work__title__column:nth-child(1) {
        font-size: 17px;
        font-weight: 600;
    }
    .work__title__column img {
        height: 14px;
        width: 14px;
        margin-left: 8px;
        margin-top: 7px;
    }
    .info {
        margin-top: 20px;
    }
    .work__title__column:nth-child(3) {
        height: 31px;
        width: 31px;
    }
    .work__title__column:nth-child(2) {
        margin-left: 15px;
        margin-right: 10px;
    }
}

.header__text {
    margin-top: 30px;
    font-size: 17px;
}

@media (max-width: 768px) {
    .header__text {
        margin-top: 10px;
        font-size: 15px;
    }
}
