.center {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
}

.portfolio__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    height: 50px;
}

.portfolio__row h2 {
    margin-left: 20px;
}

.portfolio__row img {
    cursor: pointer;
}