.footer {
    background-color: rgb(58, 98, 138);
    margin-top: 40px;
    color: white;
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
}

.contact div:not(:first-child) {
    margin-top: 31px;
}

.contact img {
    margin-right: 12px;
}

.mail path {
    fill: white;
}

.button {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.review label {
    display: block;
    margin-bottom: 3px;
}

.button button, .link a {
    background-color: white;
    color:rgb(64, 151, 243);
    padding: 7px 5px;
}

.button_2 button, .button_2 a {
    background-color: white;
    color:rgb(64, 151, 243);
    padding: 7px 5px;
    font-size: 15px;
}
.button_2 {
    display: flex;
    justify-content: space-between;
}

.review {
    display: grid;
    grid-template-columns: 300px;
    column-gap: 10px;
    row-gap: 10px;
}

.review input {
    width: calc(100% - 2px); 
    height: 25px;
}
.review textarea {
    width: 100%;
    min-height: 100px;
    resize: vertical;
}

.review p {
    margin: 0;
    font-size: 20px;
    font-weight: 600;
}

@media (max-width: 720px) {
    .footer {
        flex-direction: column-reverse;
        align-items: center;
    }
    .review {
        margin-bottom: 20px;
        grid-template-columns: 290px; 
    }
    .contact div:not(:first-child) {
        margin-top: 15px;
    }
}

@media (max-width: 480px) {
    .button_2 button, .button_2 a {
        font-size: 13px;
    }
    .contact img:first-child {
        width: 18px;
        height: 14px;
        margin-right: 3px;
    }
    .contact div:not(:first-child) {
        margin-top: 10px;
    }
    .review {
        margin-bottom: 10px;
        grid-template-columns: 260px; 
    }
    .review textarea {
        min-height: 80px;
    }
    .contact div {
        font-size: 14px;
    }
    .contact button {
        font-size: 15px;
    }
}   