.video__wrapper {
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    background-color: rgba(0,0,0,0.6);
  
}
.video {
    position: fixed;
    top: 0;
    left: 0;
    width: calc(100% - 100px);
    height: 100%;
    display: flex;
    justify-content: center;    
    align-items: center;
    padding: 0 50px;
    max-width: 100%;
}


.close__button {
    position: absolute;
    top: 15px;
    right: 30px;
    z-index: 3;
}

.video_tag {
    max-width: 100%;
    max-height: 420px;
    object-fit: cover;
}