.buttonLink {
    padding: 2px 8px;
    background-color: rgb(64, 151, 243);
    color: white;
    width: auto;
    min-height: 24px;
    border: none;
    outline: none;
    font-size: 15px;
    cursor: pointer;
    border-radius: 2px;
    text-decoration: none;
    text-align: center;
}