.header {
    display: grid;
    grid-template-columns: minmax(160px,200px) 0.6fr 1fr;
}

.header__image {
    object-fit: cover;
    border-radius: 50%;
    position: relative;
    object-position: top;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.header__image_wrapper {
    width: 132px;
    height: 132px;
    border: 2px solid;
    border-radius: 50%;
}

.name, .lastname {
    font-size: 42px;
    font-weight: 600;
    margin: 0;
    margin-top: 5px;
}

.header__column {
    align-self: flex-end;
    position: relative;
    bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__label {
    display: flex;
    align-items: center;
}

.job {
    font-size: 15px;
    font-weight: 600;
    position: relative;
    margin-left: 66px;
    bottom: 3px;
    white-space: nowrap;
    padding-right: 20px;
}

.margin_icon {
    position: relative;
    left: 30px;
    top: -3px;
    display: block;
}

.header__setting {
    cursor: pointer;
    position: relative;
    right: 5px;
}

.header__menu {
    position: absolute;
    top: 45px;
    right: 0;
    height: 50px;
    width: 150px;
    border-radius: 10px;
    background-color: white;
    z-index: 7;
    padding: 5px 11px;
    border: 2px solid black;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.header__menu label {
    color: black
}

.square {
    width: 7px;
    height: 7px;
    border: 2px solid black;
    position: absolute;
    top: -7px;
    right: 11px;
    transform: rotate(45deg);
    z-index: -1;
}

.square:after {
    content: '';
    width: 20px;
    height: 20px;
    background-color: white;
    display: block;
    position: relative;
    top: 0px;
    left: 0px
}

.header__language {
    display: flex;
    justify-content: space-between;
}




@media (max-width: 768px) {
    .header {
        grid-template-columns: minmax(150px, 200px) 0.6fr;
    }

    .header__label {
        display: block;
    }

    .header__column {
        grid-column: 2/3;
        flex-direction: column;
        justify-content: center;
        position: relative;
        left: -10px;
    }
    .margin_icon {
        left: 0;
        margin: 22px 0 12px 0;
        transform: rotate(90deg);
    }
    .job {
        margin: 0px;
        padding: 0;
        text-align: center;
    }
}

@media (max-width: 480px) {
    .header {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .header__column {
        align-self: center;
        left: 0;
        margin-top: 8px;
    }

    .name {
        text-align: center;
    }

    .name__wrapper {
        line-height: 40px;
        margin: 15px 0;
    }
}