.flex {
    display: flex;
    flex-direction: column;
    padding: 0 5px;
}

.flex h3 {
    text-align: center;
    margin: 22px 0
}

.flex img {
    width: 100%;
    height: 160px;
    object-fit: cover;
    object-position: top;
}

.flex span {
    font-weight: 600;
    font-size: 17px;
    color: inherit
}

.flex a {
    color: inherit;
    text-decoration: none;
}

.flex a:hover {
    text-decoration: underline;
}

.flex > button {
    padding: 5px 10px;
    cursor: pointer;
}

.flex button {
    color: inherit
}

.technology {
    margin: 6px 0 22px 0 
}

.button {
    background-color: transparent;
    outline: none;
    border: none;
    font-size: 16px;
}

.button:hover {
    text-decoration: underline;
    cursor: pointer;
}

.git {
    margin: 10px 0 6px 0 
}

.show {
    margin: 10px 0 10px 0 
}

@media (max-width: 580px) {
    .flex img {
        height: 200px;
    }
}

