.comment__img img{
    border-radius: 50%;
    border: 1px solid
}

.comment__grid {
    display: grid;
    grid-template-columns: 80px 1fr;
}

.comment__flex span {
    height: 30px;
}

.comment__info span:nth-child(1) {
    font-size: 17px;
    font-weight: 600;
    margin-right: 15px;
}

.comment__info span:nth-child(2) {
    font-size: 13px;
    color: rgb(97, 97, 97)
}

.comment__info p {
    margin: 4px 0;
}

.comment__grid {
    margin-top: 20px;
}

.first__comment {
    margin-top: 30px;
}

.center {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    margin-top: 50px;
}

.loading {
    display: flex;
    justify-content: center;
    margin-top: 65px;
    margin-bottom: 35px;
}

