.option {
    position: absolute;
    top: 42px;
    right: -4px;
    height: 45px;
    width: 130px;
    border-radius: 10px;
    background-color: white;
    color: black;
    z-index: 7;
    padding: 6px 10px;
    border: 2px solid black;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.option p {
    margin: 0;
    position: relative;
    cursor: pointer;
}

.option p:after {
    content: '\27A4';
    position: absolute;
    top: 0;
    right: 0

}

.option__filter {
    position: absolute;
    top: -6px;
    right: 152px;
    height: auto;
    width: 160px;
    border-radius: 10px;
    background-color: white;
    color: black;
    z-index: 7;
    padding: 6px 10px;
    border: 2px solid black;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.option__sort {
    position: absolute;
    top: -6px;
    right: 152px;
    height: auto;
    width: 160px;
    border-radius: 10px;
    background-color: white;
    color: black;
    z-index: 7;
    padding: 6px 10px;
    border: 2px solid black;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.square {
    width: 7px;
    height: 7px;
    border: 2px solid black;
    position: absolute;
    top: -7px;
    right: 11px;
    transform: rotate(45deg);
    z-index: -1;
    background-color: white;
}

.square:after {
    content: '';
    width: 20px;
    height: 20px;
    background-color: white;
    display: block;
    position: relative;
    top: 0px;
    left: 0px
}

